.horario-completo-hora {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 80px;
    border: 1px solid #000000;
    border-radius: 10px;
    background: #e0b8f3;
    opacity: .7;
}

.horario-completo-nombre-dia {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 40px;
    border: 1px solid #000000;
    border-radius: 10px;
    background: #f7a8f7;
    opacity: .7;
}

.horario-semanal {
    display: flex;
    flex-direction: row;
    gap: 8px;
}