.user-card-article {
    display: flex;
    align-items: center;
    color: #0e0c0c;
    font-size: 1.2rem;
    justify-content: space-between;
    background-color: rgba(255, 255, 255, 0.801); 
    padding: 10px; 
    border-radius: 8px; 
}
.user-card-article img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.user-card-article span {
    opacity: .8;
}

.user-card-article header {
    display: flex;
    align-items: center;
    gap: 8px;
}

.user-card-article div {
    display: flex;
    flex-direction: row;
    gap: 8px;
}

